<template>
  <v-card style="border-radius: 40px; font-family: Nunito" color="#ededed" flat :to="redireccionar ? { name: 'DashboardMenu' } : ''">
    <v-card-title style="background-color: #BBDEFB; word-break: normal!important;"> {{ menu.nombre }}
      <v-spacer/>
      <span class="caption">{{ menu.costo | moneda }}</span>
    </v-card-title>
        <v-card-text class="text-left pt-4" style="background-color: #E3F2FD;height: 100px; overflow-y: scroll">
          <v-row>
            <v-col v-for="(detalles, indexDetalles) in menu.detalles" :key="indexDetalles" cols="12" class="py-0">
              <small>{{detalles.cantidad}} pza<span v-if="detalles.cantidad > 1">s </span>  {{ detalles.producto.nombre }} </small>
            </v-col>
          </v-row>
        </v-card-text>
    <template v-if="mostrarAcciones">
      <v-card-actions v-if="opcionAgregar" class="text-center" style="display: grid; background-color: #2196f3">
        <v-btn text dark @click="$emit('seleccionar')">Agregar</v-btn>
      </v-card-actions>
      <v-card-actions v-else class="text-center" style="display: grid; background-color: #8bc34a">
        <v-btn text dark>Agregado</v-btn>
      </v-card-actions>
      <v-card-actions v-if="opcionQuitar" class="text-center" style="display: grid; background-color: #2196f3">
        <v-btn text dark>Quitar</v-btn>
      </v-card-actions>
    </template>

  </v-card>
</template>

<script>

    export default {
        name: "TarjetaMenu",
      props: {
          opcionAgregar: { type: Boolean, default: false },
          opcionQuitar: { type: Boolean, default: false },
          agregado: { type: Boolean, default: false },
          menu: {type: Object, required: true},
          cargando: { type: Boolean, default: false },
          mostrarAcciones: { type: Boolean, default: false },
          redireccionar: {type: Boolean, required: false}

      },
      data(){
          return{
            verDetalles: false
          }
      },
    }
</script>

<style scoped>
  .formaCircularMenu {
    background-color: #eaf1f6!important;
    padding-top: 0;
    border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
    -webkit-box-shadow: 0px 19px 34px -7px #81abbe;
    box-shadow: 0px 19px 34px -7px #81abbe;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>
