<template>
  <wrapper titulo="Productos">
    <template v-if="listaAlumnos.length > 0">
      <v-row justify="space-around">
        <v-col v-if="$usuario.esPadreDeFamilia" cols="12" md="4" class="text-center">
          <v-menu offset-y rounded="b-xl">
            <template v-slot:activator="{ on, attrs }">
              <v-btn v-bind="attrs" v-on="on" text outlined rounded style="border-width: 5px;">
                {{ alumnoSeleccionado.nombreCompleto }}<v-icon>mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item v-for="(alumno, index) in listaAlumnos" :key="index" link @click="seleccionarHijo(alumno)">
                <v-list-item-title>{{ alumno.nombreCompleto }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-col>
        <v-col cols="12" class="text-center">
          <panel-opciones :fecha="fecha" @cambiar-fecha="fecha = $event"
                          @seleccionar-hijo="seleccionarHijo" :saldo="saldoCafeteriaAlumnoSeleccionado" />
        </v-col>
      </v-row>

      <v-row>
        <v-col v-if="menusFiltrados.length > 0" cols="12">
          <v-fade-transition mode="out-in" hide-on-leave>
            <carrusel :key="alumnoSeleccionado.id" per-page-escritorio="2">
              <carrusel-item v-for="(menu, indexMenu) in menusFiltrados" :key="indexMenu">
                <tarjeta-menu :menu="menu" :opcion-agregar="!estaMenuEnCarrito(menu.id)" :cargando="alumnoSeleccionado.cargandoSaldo" mostrar-acciones
                              @seleccionar="seleccionarMenu(alumnoSeleccionado, fecha, menu)"/>
              </carrusel-item>
            </carrusel>
          </v-fade-transition>
        </v-col>
        <v-col v-else cols="12">
          <v-row align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
            <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
              <img src="../../assets/menu_estado_vacio.svg"
                   :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
            </v-col>
            <v-col cols="12" class="text-center">
              <h2 style="font-family: Nunito;" class="text--disabled">No hay menús disponibles para este día</h2>
            </v-col>
          </v-row>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="12">
          <carrusel>
            <carrusel-item v-for="(categoria, indexCategorias) in categorias" :key="indexCategorias" size="41%">
              <categoria :categoria="categoria"
                         :esta-seleccionada="categoriaSeleccionada && categoria.id === categoriaSeleccionada"
                         @seleccionarCategoria="seleccionarCategoria(categoria)"/>
            </carrusel-item>
          </carrusel>
        </v-col>
        <template v-if="productosFiltrados && productosFiltrados.length > 0">
          <v-col v-for="(producto, indexProducto) in productosFiltrados" :key="indexProducto" cols="12" md="6">
            <producto :producto="producto" opcion-agregar mostrar-acciones
                      :productos-bloqueados-alumno="alumnoSeleccionado.productosBloqueados"
                      :producto-agregado="estaProductoEnCarrito(producto.id)"
                      @seleccionar="seleccionarProducto(alumnoSeleccionado, fecha, producto)"/>
          </v-col>
        </template>

        <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
          <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
            <img src="../../assets/productos_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
          </v-col>
          <v-col cols="12" class="text-center">
            <h2 style="font-family: Nunito;" class="text--disabled">No hay productos disponibles</h2>
          </v-col>
        </v-row>
      </v-row>
    </template>

    <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
      <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
        <img src="../../assets/menu_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
      </v-col>
      <v-col cols="12" class="text-center">
        <h2 style="font-family: Nunito;" class="text--disabled">No tiene alumnos agregados</h2>
      </v-col>
      <v-col cols="12" md="6" class="text-center">
        <v-btn rounded :to="{ name: 'Alumnos' }"><span style="font-family: Nunito;" class="primary--text">Ir a alumnos</span></v-btn>
      </v-col>
    </v-row>
    <v-snackbar v-model="mensajeProductoAgregado" color="success" bottom app>Producto agregado</v-snackbar>
  </wrapper>
</template>

<script>
  import TarjetaMenu from "../Menu/TarjetaMenu";
  import MenusService from "../../services/MenusService";
  import Carrusel from "../General/Carrusel/Carrusel";
  import CarruselItem from "../General/Carrusel/CarruselItem";
  import Orden from "../../models/Orden";
  import Producto from "../Tienda/Producto";
  import Categoria from "../Tienda/Categoria";
  import AuthConst from "../../config/AuthConst";
  import CategoriasService from "../../services/CategoriasService";
  import ProductosService from "../../services/ProductosService";
  import FechaService from "../../services/FechaService";
  import CafeteriasService from "../../services/CafeteriasService";
  import SaldoService from "../../services/SaldoService";
  import OrdenesService from "../../services/OrdenesService";
  import UsuariosService from "../../services/UsuariosService";
  import PanelOpciones from "../General/PanelDeOpciones/PanelOpciones";

  export default {
    name: "VistaProductosParaAlumno",
    components: {PanelOpciones, CarruselItem, Carrusel, TarjetaMenu, Producto, Categoria},
    data() {
      return {
        calendario: false,
        fecha: null,
        dialogSeleccionarAlumno: false,
        menus: [],
        ordenes: [],
        listaAlumnos: [],
        alumnoSeleccionado: {},
        mensajeProductoAgregado: false,
        menusFiltrados: [],
        // Valores de tienda
        productos: [],
        categorias: [],
        categoriaSeleccionada: null,
        productosFiltrados:[],
      }
    },
    async created() {
      this.listaAlumnos = UsuariosService.getListaAlumnos(this.$usuario);
      this.alumnoSeleccionado = this.listaAlumnos.length ? this.listaAlumnos[0] : {};
      this.$loader = true
      try {
        this.menus = await MenusService.getMenusSemanales()
      } catch (e) {
        this.menus = []
      }

      try {
        const getOrdenes = OrdenesService.getPromiseOrdenesById(this.$usuario);
        this.ordenes = await getOrdenes(this.$usuario.id);
      } catch (e) {
        console.log(e)
      }

      try {
        this.productos = await ProductosService.getProductos();
      } catch (e) {
        this.productos = []
      }
      try {
        this.categorias = await CategoriasService.getCategorias();
      } catch (e) {
        this.categorias = []
      }
      if(!this.categoriaSeleccionada){
        this.productosFiltrados = this.productos
      }

      this.fecha = FechaService.getFechaString()
      this.$loader = false

      const vm = this
      if (this.$usuario.esPadreDeFamilia) {
        for (const hijo of this.$usuario.hijos) {
          vm.$set(hijo.alumno, 'cargandoSaldo', true)
          CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, hijo.alumno.id)
            .then(cafeterias => {
              vm.$set(hijo.alumno.cafeteria, 'saldoAlumno',
                vm.$getSaldoDeAlumnoParaCafeteria(hijo.alumno.cafeteria.id, cafeterias))
            })
            .catch(() => {})
            .finally(() => (vm.$set(hijo.alumno, 'cargandoSaldo', false)))
        }
      } else if (this.$usuario.esAlumno) {
        vm.$set(vm.$usuario, 'cargandoSaldo', true)
        CafeteriasService.getSaldoPorCafeteria(vm.$usuario.idCliente, vm.$usuario.id)
          .then(cafeterias => {
            vm.$set(vm.$usuario.cafeteria, 'saldoAlumno',
              vm.$getSaldoDeAlumnoParaCafeteria(vm.$usuario.cafeteria.id, cafeterias))
          })
          .catch(() => {})
          .finally(() => (vm.$set(vm.$usuario, 'cargandoSaldo', false)))
      }
    },
    watch: {
      async fecha(val) {
        this.$loader = true;
        try {
          this.filtrarMenu(val)
        } catch (e) {
          console.log(e)
        }
        this.$loader = false;
      }
    },
    computed: {
      saldoCafeteriaAlumnoSeleccionado() {
        return this.alumnoSeleccionado.cafeteria.saldoAlumno
          - SaldoService.getCostoTotalOrdenes(this.alumnoSeleccionado, this.ordenes.filter(orden => orden.estatus.titulo === 'CREADA'))
          - SaldoService.getCostoTotalOrdenes(this.alumnoSeleccionado, this.$ordenesNuevas)
      }
    },
    methods: {
      seleccionarMenu(alumno, fecha, menu) {
        if (existeMenuEnOrdenAlumno(this, menu, alumno, fecha)) {
          this.$showAlerta('600', 'warning', 'mdi-alert-circle',
            'Sólo se puede agregar un menú por día', false);
        } else {
          for (let detalle of menu.detalles) {
            if (!SaldoService.esSaldoSuficiente(this.$ordenesNuevas, alumno, detalle.producto, detalle.cantidad)) {
              this.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'El alumno no cuenta con saldo suficiente', false);
            } else if (alumno.productosBloqueados.some(pb => detalle.producto.id === pb.producto.id)) {
              this.$showAlerta('600', 'warning', 'mdi-alert-circle',
                'El producto ' + detalle.producto.nombre + ' no se ha agregado a la orden porque se encuentra bloqueado para ' + alumno.nombre, false);
            } else {
              asignarProductoEnOrden(this, alumno, fecha, { ...detalle.producto, menu}, detalle.cantidad)
            }
          }
        }
      },
      seleccionarHijo(alumno) {
        this.alumnoSeleccionado = alumno
        this.filtrarMenu(this.fecha)
      },
      filtrarMenu(fechaSeleccionada) {
        if (fechaSeleccionada && this.alumnoSeleccionado.id) {
          const vm = this
          this.menusFiltrados = vm.menus.filter(menu => {
            return (menu.esSemanal || fechaSeleccionada === menu.fecha)
              && (!vm.alumnoSeleccionado.cafeteria || vm.alumnoSeleccionado.cafeteria.id === menu.cafeteria.id)
              && (vm.alumnoSeleccionado.seccion.id === menu.seccionEscolar.id)
          })
        } else
          this.menusFiltrados = [ ]
      },
      estaMenuEnCarrito(idMenu) {
        return this.$ordenesNuevas.some(
          orden => this.fecha === orden.fecha
            && this.alumnoSeleccionado.id === orden.alumno.id
            && orden.productos.some(producto => producto.menu && idMenu === producto.menu.id)
        )
      },
      // Methods de tienda
      seleccionarCategoria(categoria) {
        let vm = this
        vm.categoriaSeleccionada = categoria.id
        if (vm.categoriaSeleccionada){
          vm.productosFiltrados = vm.productos.filter(function (productoFiltrado) {
            let idCategoriaProducto = productoFiltrado.categoria.id;
            return (idCategoriaProducto == vm.categoriaSeleccionada);
          })
        }else {
          vm.productosFiltrados = vm.productos
        }
      },
      seleccionarProducto(alumno, fecha, producto) {
        if (!SaldoService.esSaldoSuficiente(this.$ordenesNuevas, alumno, producto, 1)) {
          this.$showAlerta('600', 'warning', 'mdi-alert-circle',
            'El alumno no cuenta con saldo suficiente', false);
        }
        else if (!alumno.productosBloqueados.some(pb => producto.id === pb.producto.id)) {
          asignarProductoEnOrden(this, alumno, fecha, producto, 1)
        } else {
          this.$showAlerta('600', 'warning', 'mdi-alert-circle',
            'El producto ' + producto.nombre + ' no se ha agregado a la orden porque se encuentra bloqueado para ' + alumno.nombre, false);
        }
      },

      estaProductoEnCarrito(idProducto) {
        return this.$ordenesNuevas.some(
          orden => this.fecha === orden.fecha
            && this.alumnoSeleccionado.id === orden.alumno.id
            && orden.productos.some(producto => producto.producto && idProducto === producto.producto.id))
      }
    }
  }

  function asignarProductoEnOrden(vm, alumno, fecha, producto, cantidad) {
    const clienteInternet = vm.$usuario.esPadreDeFamilia ? vm.$usuario : null;
    let index = vm.$ordenesNuevas.findIndex(orden => orden.idAlumno === alumno.id && orden.fecha === fecha);
    if (index < 0)
      index = (vm.$ordenesNuevas.push(
        new Orden({ alumno, fecha, clienteInternet, cafeteria: alumno.cafeteria })
      )) - 1;

    const orden = vm.$ordenesNuevas[index];
    orden.productos.push({ id: 0, cantidad, producto, costo: cantidad * producto.costo, menu: producto.menu });
    vm.$set(orden, 'costo', orden.productos.reduce((total, p) => total + p.costo, 0))

    vm.mensajeProductoAgregado = true
    localStorage.setItem(AuthConst.CARRITO_KEY, JSON.stringify(vm.$ordenesNuevas))
  }

  function existeMenuEnOrdenAlumno(vm, menu, alumno, fecha) {
    return vm.$ordenesNuevas
      .some(orden => fecha === orden.fecha && alumno.id === orden.alumno.id && orden.productos.some(producto => !!(producto.menu)))
  }
</script>

<style scoped>

</style>
